@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  /* ff7878,rgb(75, 68, 68),f3f0d7 */
  --main-color: #e0875e;
  --accent-color: rgb(88, 73, 73);
  --accent-color-2: #25494c;
  --background-color: #f6f0e9;
  --main-font: "Roboto", sans-serif;
  --heading-size: 3vw;
  --body-size: 28px;
}

body {
  background-color: #f6f0e9;
  background-color: var(--background-color);
}

.main-container {
  display: grid;
  place-items: center;
  grid-row-gap: 50px;
  row-gap: 50px;
}

.heading1 {
  font-family: "Roboto", sans-serif;
  font-family: var(--main-font);
  color: #e0875e;
  color: var(--main-color);
  font-size: 3vw;
  font-size: var(--heading-size);
  font-weight: 700;
}

.heading2 {
  font-family: "Roboto", sans-serif;
  font-family: var(--main-font);
  color: rgb(88, 73, 73);
  color: var(--accent-color);
  font-size: 28px;
  font-size: var(--body-size);
  text-align: center;
  font-weight: 400;
  margin: 0 8%;
}

@media only screen and (max-width: 900px) {
  .main-container {
    padding: 25px;
  }
  .heading1 {
    font-size: 50px;
  }

  .heading2 {
    font-size: 28px;
    margin: 10px;
  }
}

@media only screen and (min-width: 900px) {
  .main-container {
    padding: 5% 25%;
  }
}

.typing {
  width: 10ch;
  -webkit-animation: typing 3s steps(20), blink 1s step-end infinite alternate;
          animation: typing 3s steps(20), blink 1s step-end infinite alternate;
  white-space: nowrap;
  overflow: hidden;
  border-right: 5px solid;
}

@-webkit-keyframes typing {
  from {
    width: 0;
  }
}

@keyframes typing {
  from {
    width: 0;
  }
}

@-webkit-keyframes blink {
  50% {
    border-color: transparent;
  }
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}

.profile-pic {
  height: 220px;
  -webkit-animation: hithere 3s ease;
          animation: hithere 3s ease;
  border-radius: 100%;
  border: 8px solid var(--main-color);
  -webkit-animation-delay: 2.5s;
          animation-delay: 2.5s;
}

@-webkit-keyframes hithere {
  30% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  40%,
  60% {
    -webkit-transform: rotate(-10deg) scale(1.05);
            transform: rotate(-10deg) scale(1.05);
  }
  50% {
    -webkit-transform: rotate(10deg) scale(1.05);
            transform: rotate(10deg) scale(1.05);
  }
  70% {
    -webkit-transform: rotate(0deg) scale(1.05);
            transform: rotate(0deg) scale(1.05);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes hithere {
  30% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  40%,
  60% {
    -webkit-transform: rotate(-10deg) scale(1.05);
            transform: rotate(-10deg) scale(1.05);
  }
  50% {
    -webkit-transform: rotate(10deg) scale(1.05);
            transform: rotate(10deg) scale(1.05);
  }
  70% {
    -webkit-transform: rotate(0deg) scale(1.05);
            transform: rotate(0deg) scale(1.05);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.tab-enter {
  opacity: 0;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}
.tab-enter-active {
  opacity: 1;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  transition: opacity 300ms, -webkit-transform 300ms;
  transition: opacity 300ms, transform 300ms;
  transition: opacity 300ms, transform 300ms, -webkit-transform 300ms;
}
.tab-exit {
  opacity: 1;
}
.tab-exit-active {
  opacity: 0;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  transition: opacity 300ms, -webkit-transform 300ms;
  transition: opacity 300ms, transform 300ms;
  transition: opacity 300ms, transform 300ms, -webkit-transform 300ms;
}

@-webkit-keyframes slide-up {
  from {
    opacity: 0.25;
    line-height: 500px;
  }
  to {
    opacity: 1;
    line-height: 45px;
  }
}

@keyframes slide-up {
  from {
    opacity: 0.25;
    line-height: 500px;
  }
  to {
    opacity: 1;
    line-height: 45px;
  }
}

