hr {
  width: 50vw;
  height: 7px;
  background-color: var(--main-color);
  border: 0px;
}

.navbar {
  display: flex;
  list-style-type: none;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-left: 0px;
}

.navbar .navitem {
  flex: 1 1;
  font-size: var(--body-size);
  color: var(--main-color);
  font-weight: 700;
  text-align: center;
  text-decoration: none;
}

.navbar .navitem,
.navbar .navitem:after,
.navbar .navitem:before {
  transition: all 0.8s;
  position: relative;
  z-index: 1;
}
.navbar .navitem:hover {
  color: var(--accent-color);
}

.navbar .navitem:after {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  height: 100%;
  width: 1px;
  content: ".";
  color: transparent;
  background: var(--main-color);
  visibility: none;
  opacity: 0;
  z-index: -1;
}

.navbar .navitem:hover:after {
  opacity: 1;
  visibility: visible;
  width: 100%;
}

@media only screen and (max-width: 900px) {
  .navbar {
    flex-wrap: wrap;
    flex-direction: column;
  }

  .navbar .navitem {
    font-size: 28px;
  }

  hr {
    width: calc(var(--body-size) * 11);
  }
}

@media only screen and (min-width: 900px) {
  .navbar .navitem {
    font-size: 28px;
  }

  /* hr {
    width: 90vw;
  } */
}

.about-container {
  font-family: var(--main-font);
  color: var(--accent-color);
  font-size: var(--body-size);
  text-align: left;
  font-weight: 300;
  justify-self: left;
}

@media only screen and (max-width: 750px) {
  .about-container {
    font-size: 26px;
    margin: 0px;
  }
}

.contact-container {
  font-family: var(--main-font);
  color: var(--accent-color);
  font-size: var(--body-size);
  text-align: center;
  font-weight: 300;
}

.flex-container {
  display: flex;
  justify-content: center;
}
.copy-container {
  display: inline-block;
}
.button-container {
  list-style: none;
}

.button {
  display: block;
  text-align: center;
  width: 65px;
  height: 65px;
  padding: 15px;
  margin: 10px;
  border-style: solid;
  border-width: 0 0 5px;
  transition-property: box-shadow, border, margin;
  transition-duration: 250ms;
  position: relative;
  border-radius: 25px;
}
.button:active {
  margin-top: 13px;
}
.button .button-image {
  fill: #fff;
  transition-property: top;
  transition-duration: 250ms;
}
.button .button:active .button-image {
  top: 26px;
}
.copy {
  width: 35px;
  height: 35px;
  padding: 3px;
  margin: 0px;
}

.button.copy {
  background: #e0875e;
  border-color: #c76c42;
  border-radius: 10px;
  border-width: 0 0 3px;
  transition-duration: 100ms;
  margin-top: 8px;
}

.button.copy:active {
  box-shadow: 0 -1px 0 1px #c76c42;
  border-width: 0 0 0;
  margin-top: 9px;
}

.button.insta {
  background: #dd2a7b;
  border-color: #a31957;
}
.button.insta:active {
  box-shadow: 0 -1px 0 1px #a31957;
  border-width: 0 0 0;
}
.button.linkedin {
  background: #0e76a8;
  border-color: #005d8f;
}
.button.linkedin:active {
  box-shadow: 0 -1px 0 1px #005d8f;
  border-width: 0 0 0;
}

.button.github {
  background: #222222;
  border-color: #111;
}
.button.github:active {
  box-shadow: 0 -1px 0 1px #111111;
  border-width: 0 0 0;
}

@media only screen and (max-width: 750px) {
  .contact-container {
    font-size: 26px;
    margin: 0px;
  }
}

.work-wrapper h1 {
  text-align: center;
  color: var(--accent-color-2);
  font-size: calc(var(--body-size) * 0.9);
}
.work-container {
  font-family: var(--main-font);
  color: var(--accent-color-2);
  text-align: left;
  font-weight: 300;
  margin: 0 8%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 20px;
  gap: 20px;
}

.work-item {
  background-color: rgba(255, 255, 255, 0.514);
  flex: 1 1 350px;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.15);
  border-radius: 25px;
}

.exp {
  background-color: var(--accent-color-2);
}

.proj {
  background-color: var(--main-color);
}

#proj-heading {
  color: var(--main-color);
}
.work-heading {
  padding: 20px;
  border-radius: 20px 20px 0px 0px;
  color: var(--background-color);
  font-size: calc(var(--body-size) * 0.65);
}

.work-desc {
  padding: 0 20px 20px 20px;
  font-size: calc(var(--body-size) * 0.5);
}

.btn-container {
  margin: 25px 0 10px 0;
}

.code-btn {
  border-radius: 10px;
  position: relative;
  border: 0;
  padding: 10px 15px;
  text-align: center;
  color: white;
  box-shadow: 0px 4px 0px #706f6f;
  background-color: #838383;
  text-decoration: none;
  width: 100px;
}

.code-btn:active {
  box-shadow: 0 0 #87a86f;
  background-color: #87a86f;
  top: 4px;
}
@media only screen and (max-width: 750px) {
  .work-container {
    margin: 0px;
  }

  .work-wrapper h1 {
    text-align: center;
    color: var(--accent-color-2);
    font-size: 28px;
  }

  .work-heading {
    font-size: 18px;
  }

  .work-desc {
    font-size: 16px;
  }
}

@media only screen and (min-width: 750px) {
  .work-container {
    margin: 0px;
  }

  .work-wrapper h1 {
    text-align: center;
    color: var(--accent-color-2);
    font-size: 30px;
  }

  .work-heading {
    font-size: 24px;
  }

  .work-desc {
    font-size: 17px;
  }
}

